<script>
    import { filters, studies, filterCategories } from '../store';
    import Study from '../Study.svelte';
    import Loader from '../shared/Loader.svelte';
    import { flip } from 'svelte/animate';
import { onMount } from 'svelte';

    let animating = false;
    const DURATION = 300;
    let isLoading = true;

    function handleFilter(cat) {
        if(animating) return;

        if($filters.includes(cat)){
            $filters = $filters.filter( (f) => {
                return f !== cat;
            } )
        }else {
            $filters = [...$filters, cat];
        }
    }

    function clickBlock() {
        animating = true;
        
        setTimeout(() => animating = false, DURATION);
    }

    function checkFilters($filters){
        clickBlock();
        let tempStudies = $studies;

        tempStudies.map( s => {
            if($filters.length === 0) return s.show = true;
            let hasOne = false;

            for(let i=0; i<s.technologies.length; i++) {
                if($filters.includes(s.technologies[i].name)) {
                    hasOne = true;
                    break;
                }
            }

            if(hasOne) return s.show = true;
            return s.show = false;
        })

        $studies = tempStudies;
    }

    $: checkFilters($filters);

    onMount(async() => {
        try{
            const response = await fetch('https://andrereitz.com.br/wp-json/reitz/studies');
    
            if(response.ok) {
                const data = await response.json();
                studies.set(data.map( d => ({...d, show: true}) ));
            } else {
                throw new Error(response.status + " Failed Fetch");
            }
        } catch(err) {
            console.log('Error: ', err)
        } finally {
            isLoading = false;
        }
    })
</script>

<div class="container">
    <h2><span class="font-light">My</span> Studies</h2>
    <div class="filter-nav">
        {#if $filterCategories.length > 0}
        <h4>
            { #if $filters.length === 0 }
                Filter:
            { :else }
                <span on:click="{ () => $filters = [] }">Clear filters</span>
            { /if }
        </h4>
            <ul class="filter">
                { #each $filterCategories as cat }
                    <li on:click="{ () => handleFilter(cat.name) }" data-filter="{cat.name}" class="{ $filters.includes(cat.name) ? 'active' : '' }">{ cat.name }</li>
                { /each }
            </ul>
        {:else}
            <Loader text='Loading filters' />
        {/if}
    </div>
    <div class="studies">
        { #each $studies.filter( s => s.show == true) as study (study.id) }
            <div class="study-container" animate:flip="{{ duration: DURATION }}">
                <Study {study} />
            </div>
        { /each }
        {#if isLoading}
            <div class="loader-wrapper">
                <Loader centered spinnerBg="#808080" />
            </div>
        {/if}
        {#if $studies.filter( s => s.show == true).length === 0 && !isLoading}
            No study with selected filters
        {/if}
    </div>
</div>

