                letter-spacing: 2px;
                cursor: pointer;
            }
        }
        ul {
            flex: 3 1 100%;
            width: 100%;
            display: inline-block;
            margin-left: 15px;

            li {
                float: left;
                padding: 5px 15px;
                border-radius: 500px;
                border: 1px solid var(--color-gray-300);
                margin-right: 10px;
                color: var(--color-gray-350);
                cursor: pointer;
                margin: 5px 10px 5px 0;

                &.active {
                    color: var(--color-white);
                    background: linear-gradient(135deg, #9D34CE 0.42%, #A65FD6 0.43%, #0F90C0 100%);
                }
            }
        }

        @media screen and (max-width: 768px) {
            flex-flow: row wrap;

            h4,
            ul {
                flex: 1 1 100%;
